<template>
	<div id="imessage">
		<div class="content" :class="type">
			<div class="icon-area">
				<i class="icon icon-icon"></i>
			</div>
			<div class="text-area">
				<p class="msg-title">{{message}}</p>
				<p class="desc" v-if="description">{{description}}</p>
			</div>
			<div class="btn-area">
				<div class="button" @click="onJsUrl(redirect)">{{btnText}}</div>
			</div>
			<div class="extra-area">
				<div @click="onJsUrl('/pages/order/takeout');">返回首页</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				type: 'info',
				message: '提示信息',
				description: '描述信息',
				redirect: '/pages/order/takeout',
				btnText: '确定',
				show: true
			}
		},
		methods: {
			onJsUrl(url) {
				this.util.jsUrl(url);
				setTimeout(() => {
					this.$destroy(true)
					this.$el.parentNode.removeChild(this.$el) // 从DOM里将这个组件移除
				}, 100)
			}
		},
	}
</script>

<style scoped>
	#imessage{
		position: fixed;
		z-index: 1000000;
		background-color: #fff;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
	}
	#imessage .content{
		background-color: #fff;
		text-align: center;
		padding: 0 30px;
	}
	#imessage .content .icon-area{
		padding: 35px 0;
	}
	#imessage .content.error .icon-icon:before{
		font-size: 104px;
		color: #f76260;
		content: "\e61a";
	}
	#imessage .content.info .icon-icon:before {
		font-size: 104px;
		color: #10aeff;
		content: "\e61c";
	}
	#imessage .content.success .icon-icon:before {
		font-size: 104px;
		color: #09BB07;
		content: "\e619";
	}
	#imessage .content .text-area{
		padding: 0 20px;
		margin-bottom: 25px;
		line-height: 1.5;
	}
	#imessage .content .text-area .msg-title {
		margin-bottom: 5px;
		font-weight: 400;
		font-size: 20px;
		margin-top: 0;
	}
	#imessage .content .text-area .desc {
		font-size: 14px;
		color: #888;
	}
	#imessage .content .btn-area .button{
		height:42px;
		line-height: 42px;
		background: #f7f7f7;
		border-radius: 5px;
	}
	#imessage .content.success .btn-area .button{
		background: #09BB07;
		color: #fff;
		border-color: #09BB07;
	}
	#imessage .content.info .btn-area .button{
		background: #10aeff;
		color: #fff;
		border-color: #10aeff;
	}
	#imessage .content.error .btn-area .button{
		background: #f76260;
		color: #fff;
		border-color: #f76260;
	}
	#imessage .content .extra-area{
		position: fixed;
		left: 0;
		bottom: 0;
		width: 100%;
		text-align: center;
		margin-bottom: 15px;
		line-height: 1.5;
	}
	#imessage .content .extra-area div{
		color: #61749B;
		font-size: 14px;
	}
</style>


