<template>
	<div id="failed-tips" :class="failedTips.position">
		<div class="wrapper">
			<img :src="'static/img/tips/' + failedTips.type + '.png'" alt="">
			<p class="tips">{{failedTips.tips}}</p>
			<a @click="util.jsUrl(failedTips.link)" class="link" v-if="failedTips.link && failedTips.btnText">{{failedTips.btnText}}</a>
		</div>
	</div>
</template>

<script>
	export default {
		props: {
			failedTips: {
				type: Object,
				default() {
					return {
						type: 'message',
						tips: '获取定位失败!您可以选择手动搜索地址',
						btnText: '手动搜索地址',
						link: '/pages/home/location',
						position: 'fixed', //fixed铺满全屏 absolute适用于父级有定位且高度固定 relative适用于父级无定位
					}
				}
			},
			position: {
				type: String,
				default: 'fixed'
			}
		},
		data() {
			return {}
		},
	}
</script>

<style>
	#failed-tips{
		position: fixed;
		z-index: 1000;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		background-color: #f5f5f5;
	}
	#failed-tips.fixed{
		position: fixed;
	}
	#failed-tips.absolute{
		position: absolute;
	}
	#failed-tips.relative{
		position: relative;
	}

	#failed-tips.fixed .wrapper, #failed-tips.absolute .wrapper{
		position: absolute;
		top: 40%;
		left: 0;
		right: 0;
		transform: translateY(-50%);
		text-align: center;
		font-size: 16px;
		color: #768491;
	}
	#failed-tips.fixed .wrapper img, #failed-tips.absolute .wrapper img{
		width: 120px;
		height: 120px;
		border: 0px;
	}
	#failed-tips.fixed .wrapper .tips, #failed-tips.absolute .wrapper .tips{
		padding: 20px;
		line-height: 1.8;
	}
	#failed-tips .wrapper .link{
		display: inline-block;
		padding: 10px 16px;
		background: #0EC3B3;
		color: #fff;
		border-radius: 3px;
	}
	/*某个元素下的failedTips*/
	#failed-tips.relative .wrapper{
		width: 100%;
		text-align: center;
		padding: 20px 0;
	}
	#failed-tips.relative .wrapper img{
		max-width: 25%;
	}
	#failed-tips.relative .wrapper .tips{
		margin: 20px 0 15px;
	}

</style>


