import Vue from 'vue'
const ImessageConstructor = Vue.extend(require('./imessage.vue').default) // 直接将Vue组件作为Vue.extend的参数

const Imessage = (message) => {

	const ImessageInstance = new ImessageConstructor({
		data: Object.assign({},message)
	})
	ImessageInstance.vm = ImessageInstance.$mount() // 挂载但是并未插入dom，是一个完整的Vue实例
	ImessageInstance.vm.visible = true
	ImessageInstance.dom = ImessageInstance.vm.$el
	document.body.appendChild(ImessageInstance.dom) // 将dom插入body

	return ImessageInstance.vm
}

export default {
	install: Vue => {
		Vue.prototype.$imessage = Imessage // 将Imessage组件暴露出去，并挂载在Vue的prototype上
	}
}


